  align-items: flex-start;
  position: relative;
}

.IndependentIE_SubscribeNewsLetterSection_InnerDiv_Text_H1 {
  margin-bottom: 6px;
  font-size: 32px;
  line-height: 40px;
  font-weight: 500;
}

.IndependentIE_SubscribeNewsLetterSection_InnerDiv_Text_H2 {
  margin-bottom: 12px;
