  line-height: 20px;
  font-weight: 400;
  outline: none;
  border: 1px solid black;
}

.IndependentIE_SubscribeNewsLetterSection_InnerDiv_Text_EmailBox input:focus {
  box-shadow: rgba(211, 211, 211, 0.25) 0px 30px 60px -12px inset, rgba(211, 211, 211, 0.25) 0px 18px 36px -18px inset;
}

.IndependentIE_SubscribeNewsLetterSection_InnerDiv_Text_EmailBox button {
  border-radius: 5px;
  background-color: black;
  color: white;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 88px;
  height: 44px;
  cursor: pointer;
}

.IndependentIE_SubscribeNewsLetterSection_InnerDiv_Text_EmailBox button:hover {
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
}

.IndependentIE_SubscribeNewsLetterSection_InnerDiv_Text_ErrorBox {
  font-size: 13px;
  line-height: 16px;
  font-weight: 600;
  color: red;
}

.IndependentIE_SubscribeNewsLetterSection_InnerDiv_Text_EmailBox_Input {
  color: red;
  border: 1px solid red;
}

.IndependentIE_SubscribeNewsLetterSection_InnerDiv_Text_ErrorBox_Empty {
