.IndependentIE_TwoBySixSection {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-column-gap: 22px;
	grid-row-gap: 39px;
}

.IndependentIE_TwoBySixSection_SingleItem {
	display: flex;
	flex-direction: column;
	row-gap: 14px;
}

.IndependentIE_TwoBySixSection_SingleItem_ImgHolder {
	position: relative;
	cursor: pointer;
}

.IndependentIE_TwoBySixSection_SingleItem_TextHolder {
	cursor: pointer;
	display: flex;
	flex-direction: column;
	row-gap: 7px;
}

// bootstrap media queries
@media all and (max-width: 1023px) {
	.IndependentIE_TwoBySixSection {
		grid-template-columns: 1fr;
	}

	.IndependentIE_TwoBySixSection_SingleItem {
		flex-direction: row;
		row-gap: 0px;
		column-gap: 25px;
	}

	.IndependentIE_TwoBySixSection_SingleItem_ImgHolder {
		max-width: 25%;
		min-width: 25%;
		height: fit-content;
	}
}
