.bufferHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0px auto;
  max-width: 1248px;
  padding: 20px;
  z-index: 999;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  transition: transform 350ms ease-out 0s, box-shadow 350ms ease-out 0s;
  background-color: rgb(255, 255, 255);
  box-shadow: none;
}

.bufferHeader_isActive {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 4px;
}

.bufferHeader_hidden {
  transform: translateY(-100%);
}

.dropDownMenu {
