.navBlueLineHover {
  border-bottom: 0px solid rgb(44, 75, 255);
  width: 0px;
  height: 0px;
  position: absolute;
  display: block;
  left: calc(50% - 10px);
  top: 70%;
  transition: 0.1s ease-out;
}

.navSpan:hover > .navBlueLineHover {
  height: 5px;
  border-bottom: 5px solid rgb(44, 75, 255);
