.IndependentIE_PhotoSmallIconContainer {
	position: absolute;
	left: 0;
	bottom: 3px;
	padding: 7px;
	box-sizing: border-box;
}

.IndependentIE_PhotoSmallIconContainer_Image {
	position: absolute;
	z-index: 2;
	left: 6px;
	bottom: 5px;
}

.IndependentIE_PhotoSmallIconContainerBackground {
	background-color: black;
	opacity: 0.5;
	width: 40px;
	height: 40px;
	position: absolute;
	left: 0;
	z-index: 1;
	left: 0px;
	bottom: 0px;
}
