.HomeContainer {
  padding: 50px;
}

.HomeContainer_Heading2 {
  margin-top: -10px;
  margin-bottom: 30px;
}

.HomeContainer_ProjectSection {
  display: flex;
  -moz-column-gap: 20px;
       column-gap: 20px;
  margin-top: 30px;
  margin-bottom: 30px;
}

.HomeContainer_ProjectSection_TextHolder_Links {
  color: blue;
}

.HomeContainer_ProjectSection_TextHolder_Links:hover {
  color: darkblue;
}

.HomeContainer_ProjectSection_TextHolder li {
  margin-bottom: 10px;
}

.HomeContainer_ProjectSection_TextHolder li > ul {
  margin-top: 10px;
}

.HomeContainer_ProjectSection_ImageHolder {
  max-width: 500px;
  max-height: 300px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.HomeContainer_ProjectSection_ImageHolder:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

@media all and (max-width: 992px) {
  .HomeContainer_ProjectSection {
    flex-direction: column;
    -moz-column-gap: 0px;
         column-gap: 0px;
    row-gap: 20px;
  }
}
.HomeContainer_Footer {
  height: 200px;
